import { FC } from "react";
import styled from "styled-components";
import { ReactComponent as Design } from "./design.svg";
interface Props {
  title: string;
  desc: string;
  img: string;
}

const Svg = styled(Design)`
  width: 100%;
  height: 100%;
`;
const Item = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 1rem 1rem 1rem 1rem;
  align-items: center;
  p {
    color: #777;
  }
`;
export const GridItem: FC<Props> = ({ title, desc, img }) => {
  return (
    <Item>
      <Svg />
      <h1>{title}</h1>
      <p>{desc}</p>
    </Item>
  );
};
