import { FC } from "react";
import { GridItem } from "./GridItem";
import styled from "styled-components";
interface Props {}

const GridContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10rem;
`;

const GridRow = styled.div`
  display: flex;
  width: 100%;
  @media screen and (max-width: 700px) {
    flex-direction: column;
  }
`;
export const Grid: FC<Props> = (props) => {
  return (
    <div>
      <GridContainer>
        <GridRow>
          <GridItem title="UI/UX" desc="Wireframing, Prototyping" img="test" />
          <GridItem title="Branding" desc="Visual Identity" img="test" />
          <GridItem
            title="Development"
            desc="Responsive Full-Stack Web Apps"
            img="test"
          />
        </GridRow>
        <GridRow>
          <GridItem title="Design" desc="Illustrations" img="test" />
          <GridItem
            title="Video Editing"
            desc="Video Capture And Editing"
            img="test"
          />
          <GridItem title="Photography" desc="Product Photography" img="test" />
        </GridRow>
      </GridContainer>
    </div>
  );
};
