import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Nav } from "./components/Nav";
import { Home } from "./components/home/Home";
import styled from "styled-components";

const Container = styled.div`
  padding: 10rem 10vw;
`;

function App() {
  return (
    <Router>
      <Nav />
      <Container>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
        </Switch>
      </Container>
    </Router>
  );
}

export default App;
